import './style.scss';
import { useState, useEffect } from 'react';

export const Calculator = () => {
    let [result, setResult] = useState('');

    useEffect(() => {
        if (result[0] === '0'
        || result[0] === '+' 
        || result[0] === '-' 
        || result[0] === '*' 
        || result[0] === '/') {
           setResult('')
        }

    },[result])

    const onClickHandler = (e) => {
        setTimeout(() => {
            setResult(result.concat(e.target.innerHTML));
        },150)        
    }
        

    const clear = () => {
        setResult('');
    }

    const backspace = () => {
        setResult(result.slice(0,-1));
    }

    const calculate = () => {

        try {
            setResult(eval(result).toString());
        } catch (err) {
            setResult('ERROR');
        }
        
    }


    return (
        <div className='container'>
            <div className='calculator border'>
                <input type="text" className='screen' value={result.substring(0,14)} />
                <div className="buttons">
                    <div className='clear red' onClick={() => clear()}  >CLEAR</div>
                    <div onClick={(e) => {backspace()}}>C</div>
                    <div className='blue' onClick={(e) => {onClickHandler(e)}}>/</div>
                    <div onClick={(e) => {onClickHandler(e)}}>7</div>
                    <div onClick={(e) => {onClickHandler(e)}}>8</div>
                    <div onClick={(e) => {onClickHandler(e)}}>9</div>
                    <div className='blue' onClick={(e) => {onClickHandler(e)}}>*</div>
                    <div onClick={(e) => {onClickHandler(e)}}>4</div>
                    <div onClick={(e) => {onClickHandler(e)}}>5</div>
                    <div onClick={(e) => {onClickHandler(e)}}>6</div>
                    <div className='blue' onClick={(e) => {onClickHandler(e)}}>-</div>
                    <div onClick={(e) => {onClickHandler(e)}}>1</div>
                    <div onClick={(e) => {onClickHandler(e)}}>2</div>
                    <div onClick={(e) => {onClickHandler(e)}}>3</div>
                    <div className='blue' onClick={(e) => {onClickHandler(e)}}>+</div>
                    <div onClick={(e) => {onClickHandler(e)}}>.</div>
                    <div onClick={(e) => {onClickHandler(e)}}>0</div>
                    <div className='result blue' onClick={calculate}>=</div>
                </div>
            </div>            
        </div>
    )
}